import {useState} from 'react';
import { RiMenuUnfoldLine, RiMenuFoldLine} from 'react-icons/ri';
const CustomToolbar = ( {addTimer} )=>{
  const [formData, setFormData] = useState({
    description:"",
    time:60
  });

  const [toggleView, setToggleView] = useState(false);

  const _addTimer = () => {
    if(addTimer && true){
      addTimer(formData);
    } else {
      console.log("No Timer Handler Assigned");
    }
  }

  const onChange=({target})=>{
    const {name, value} = target;
    setFormData(
      {...formData, [name]:(name==='time')?value*60:value}
    )
  }
  const ToolbarClasses = ["ToolBar", toggleView?"":"hide"];
  return (
    <section className={ToolbarClasses.join(' ').trim()}>
      <div className="goRight">
        <div onClick={() => { setToggleView(!toggleView); }}>{toggleView ? (<RiMenuFoldLine />) : (<RiMenuUnfoldLine />)}</div>
      </div>
      <div>
        <label>Timer Name</label>
        <input type="text" name="description" value={formData.description} onChange={onChange} ></input>
      </div>
      <div>
        <label>Timer Minutes</label>
        <input type="number" min="1" max="1140" step="1" value={formData.time/60} name="time" onChange={onChange}></input>
      </div>
      <div>
      <button onClick={_addTimer}>Add New Timer</button>
      </div>
    </section>
  );
}

export default CustomToolbar;
